adas en forma redonda con un espesor aproximadamente de 10 mm.")])])]),_c('div',{staticClass:"row",attrs:{"numero":"6","titulo":"Cubos"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',[_vm._v("Las frutas después de ser peladas son cortadas en cubos con un espesor aproximadamente de 10 mm.")])])])])],1)]),_vm._m(7),_c('p',{staticClass:"my-3"},[_vm._v("La fruta se debe llevar al agua caliente por un tiempo determinado, para esto tenga en cuenta los tiempos de escaldado vistos en la actividad de aprendizaje 3, este método se hace para inactivar enzimas, ablandar la fruta y reducir los microorganismos, recuerde que el tiempo de escaldado se debe realizar dependiendo de la madurez y la firmeza de la fruta.")]),_vm._m(8),_c('p',{staticClass:"my-3"},[_vm._v("Seleccione el tipo de almíbar que quiere preparar (espeso, medio o ligero). Al elaborar el almíbar disuelva el azúcar en el agua y llévela al fuego mezclando constantemente hasta que espese. Recuerde que este también se puede preparar con el jugo de las frutas en lugar del agua y se le debe adicionar azúcar, pero tenga en cuenta el azúcar de la fruta para que no se pase de dulzor.")]),_vm._m(9),_vm._m(10),_vm._m(11),_vm._m(12),_c('p',{staticClass:"my-3"},[_vm._v("La mermada es un producto obtenido de llevar la concentración de un jugo de fruta o frutas troceadas, al cual se le adiciona azúcar y otros aditivos como la pectina, hasta lograr un producto con consistencia gelatinosa.")]),_vm._m(13),_c('div',{staticClass:"row justify-content-center my-3"},[_c('div',{staticClass:"col-xl-8 col-lg-8 justify-content-center align-content-center"},[_c('AcordionA',{staticClass:"mb-5",attrs:{"tipo":"a","clase-tarjeta":"tarjeta acordeonA"}},[_c('div',{staticClass:"row justify-content-center",attrs:{"titulo":"1. Lavado"}},[_c('div',{staticClass:"col-xl-12 col-lg-12 col-md-12 align-content-center"},[_c('p',[_vm._v("Las frutas se lavan con suficiente agua para retirar suciedad.")])])]),_c('div',{staticClass:"row justify-conte